import React, { useContext, useEffect } from "react";
import "./Navigation.scss";
import { NavLink, useLocation } from "react-router-dom";
// import Logo from "../../assets/Logo.png";
import { AuthContext } from "../../auth/AuthContext";
import { NotificationContext } from "../notification/NotificationContext";
// navigation for devices that have width 1025 px  with or more
const Navigation = () => {
  const [isAuth, , , , , , logout, authCheck] = useContext(AuthContext);
  const [, , , , notify] = useContext(NotificationContext);
  useEffect(() => {
    authCheck();
  }, [authCheck]);
  // useLocation is used for styling active component
  const { pathname } = useLocation();
  return (
    <div className="navigation">
      <div className="nav-menu">
        <NavLink
          to="/"
          exact
          activeClassName="active-nav"
          className="no-decoration"
        >
          <div className="nav-menu-item">Home</div>
        </NavLink>
        <div className="nav-menu-item categories ">
          Genres
          <div className="categories-list">
            <NavLink to="/books/all" className="categories-list-item">
              All
            </NavLink>
            <NavLink to="/books/action" className="categories-list-item">
              Action and Adventure
            </NavLink>
            <NavLink to="/books/crime" className="categories-list-item">
              Crime
            </NavLink>
            <NavLink to="/books/drama" className="categories-list-item">
              Drama
            </NavLink>
            <NavLink to="/books/fantasy" className="categories-list-item">
              Fantasy
            </NavLink>
            <NavLink to="/books/mystery" className="categories-list-item">
              Mystery
            </NavLink>
            <NavLink to="/books/romance" className="categories-list-item">
              Romance
            </NavLink>
            <NavLink to="/books/thriller" className="categories-list-item">
              Thriller
            </NavLink>
            <NavLink to="/books/other" className="categories-list-item">
              Other
            </NavLink>
          </div>
        </div>

        {isAuth ? (
          <div
            role="menuitem"
            tabIndex={0}
            className="no-decoration nav-menu-item"
            type="button"
            onClick={() => {
              logout();
              notify("You have been logged out");
            }}
            onKeyDown={() => {
              logout();
              notify("You have been logged out");
            }}
          >
            Log out
          </div>
        ) : (
          <NavLink
            to="/login"
            isActive={() => {
              return pathname === "/login" || pathname === "/signup";
            }}
            exact
            className="no-decoration "
            activeClassName="active-nav"
          >
            <div className="nav-menu-item"> Log in</div>
          </NavLink>
        )}
        <NavLink
          to="/cart"
          exact
          activeClassName="active-nav"
          className="no-decoration last"
        >
          <div className="nav-menu-item nav-menu-last-item">Cart</div>
        </NavLink>
      </div>
      {/* <div className="logo">
        <img src={Logo} alt="logo" className="logo-img" />
      </div> */}
    </div>
  );
};

export default Navigation;
