import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import authImg from "../assets/authImage.svg";
import "./PasswordReset.scss";

const PasswordReset = () => {
  const [email, setEmail] = useState("");
  const [emailTouched, setEmailTouched] = useState(false);
  const [emailValid, setEmailValid] = useState(false);
  const [emailSent, setEmailSent] = useState(false);
  const emailRegex = /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/;

  const emailErrorMsg =
    !emailValid && emailTouched ? "enter valid email" : null;
  useEffect(() => {
    if (emailRegex.test(email)) {
      setEmailValid(true);
    } else setEmailValid(false);
    return () => {
      setEmailTouched(true);
    };
  }, [email, emailRegex]);
  return (
    <>
      <img className="reset-img " src={authImg} alt="auth" />
      {emailSent ? (
        <div className="on-reset">
          <div className="on-reset-text">Email sent</div>
          <NavLink className="on-reset-link" to="/login">
            Click here to log in
          </NavLink>
        </div>
      ) : (
        <div className="reset">
          <h1>E-mail:</h1>
          <input
            className="reset-input"
            onChange={e => setEmail(e.target.value)}
          />
          <p className="reset-input-error">{emailErrorMsg}</p>
          <button
            className="reset-button"
            type="button"
            onClick={() => setEmailSent(true)}
            disabled={!emailValid}
          >
            Reset password
          </button>
          <NavLink className="reset-to-login" to="/login">
            Oh I remebered it now,back to log in!
          </NavLink>
        </div>
      )}
    </>
  );
};

export default PasswordReset;
